@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;500&display=swap);
.author{margin-left:48px;margin-right:48px;padding-top:16px}.author__link{color:inherit}

.footer{background-color:#313131;padding:16px 48px;margin-top:48px;color:#ffffff}.footer__link{color:inherit}

.header{width:100vw;padding:16px 52px;font-size:16px;box-sizing:border-box;background-color:#dadada}


.background{width:100vw;height:70vh;background-color:#dddddd;background-size:cover;margin-top:32px}.background__credits{text-align:right;padding:16px 48px;background-color:#e6e6e6}

.definition{background-color:#dbdbdb;padding:3px 6px}

.image{max-width:100vw}.image-container{width:100vw;text-align:center}

.subtitle{font-size:24px;font-weight:500;margin-top:48px;margin-left:48px;margin-right:48px}

.text{font-size:16px;font-weight:400;line-height:1.4;margin-left:48px;margin-right:48px;margin-top:12px;margin-bottom:12px;text-align:justify}

.title{font-family:'Work Sans', sans-serif;font-size:48px;font-weight:500;color:#000000;margin-left:48px;margin-right:48px;margin-top:32px}.title2{font-family:'Work Sans', sans-serif;font-size:36px;font-weight:500;color:#7B7B7B;margin-left:48px;margin-right:48px;margin-top:8px}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

body{background-color:#F2F2F2}ul{list-style-type:square;list-style-position:inside;list-style:inside;margin-left:8px}

body{font-family:"Work Sans", sans-serif;font-weight:400}

